.About {
  animation-name: fadeIn;
  animation-duration: 500ms;
  height: fit-content;
  margin: 50px 0 0 0;
  // border: 1px solid red;

  &__Heading {
    font-size: 45px;
  }

  &__Advisors {
    height: fit-content;
    // border: 1px solid black;
    width: 60%;
    margin: 70px auto 120px auto;
    // border: 10px solid
    //   linear-gradient(
    //     90deg,
    //     rgba(46, 152, 172, 1) 0%,
    //     rgba(78, 36, 147, 1) 100%
    //   );
    border: 5px solid rgba(46, 152, 172, 1);
    border-radius: 50px 0 50px 0;
    padding: 50px;
    transition: all 500ms;

    &:hover {
      border: 5px solid rgba(78, 36, 147, 1);
    }

    &--Advisor {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: left;
      width: 400px;
      margin: 0 auto;
    }

    &--Info {
      margin: 0 0 0 50px;
      // border: 1px solid red;

      &--name {
        font-size: 20px;
        margin: 10px 0;
        // border: 1px solid black;
      }

      &--post {
        // border: 1px solid grey;
      }
    }
  }

  &__Founders {
    margin: 50px auto 100px auto;
    height: 300px;
    background-color: #2c2c2c;
    width: 60%;
    display: grid;
    padding: 50px 0;
    grid-template-columns: 1fr 1fr 1fr;
    border-radius: 50px 0 50px 0;

    &--founder {
    }

    &--photo {
      height: 150px;
      width: 150px;
      border: 3px solid grey;
      margin: 0 auto;
      border-radius: 200px;
      overflow: hidden;
      transition: all;
      transition-duration: 1s;

      &:hover {
        border: 3px solid #f65354;
      }

      & > img {
        transition: all;
        transition-duration: 5s;

        filter: grayscale(0%);
        &:hover {
          transform: scale(1.5);
          filter: grayscale(100%);
        }
      }
    }

    &--info {
      margin: 20px 0 20px 0;
      color: #fd4d4d;
      font-size: 15px;
      font-weight: 550;
    }

    &--subinfo {
      margin-top: 5px;
      color: rgb(218, 218, 218);
      font-size: 12px;
    }
  }
}

.Advisor {
  &__Image {
    border-radius: 200px;
    border: 5px solid black;
  }
}
