.Launchpad {
  //   border: 1px solid red;
  width: 80%;
  margin: 100px auto;
  display: flex;
  align-items: center;
  //   box-shadow: 0 6px 5px 0 rgba(58, 58, 59, 10%);
  padding: 10px;
  border-radius: 8px;
  //   border: 1px solid #e7e7e7;

  transition: transform 0.5s;

  &::after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity 2s cubic-bezier(0.165, 0.84, 0.44, 1);
    box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.15);
    content: "";
    opacity: 0;
    z-index: -1;
    border-radius: 8px;
  }

  &:hover,
  &:focus {
    transform: scale3d(1.006, 1.006, 1);
    border-radius: 8px;

    &::after {
      opacity: 1;
    }
  }

  //   &:hover {
  //     box-shadow: 42px 35px 0 rgba(62, 58, 73, 0.27);
  //   }

  &__Right {
    // border: 1px solid red;
    width: 100%;
    margin-left: 50px;
    &--heading {
      font-size: 30px;
      font-weight: 600;
      color: rgb(65, 64, 64);
      margin: 20px 0;
      width: fit-content;
    }

    &--text {
      font-size: 17px;
      line-height: 130%;
      text-align: left;
      //   border: 1px solid red;
    }
  }
}
